<template>
  <div class="d-flex" :class="this.$store.state.menuToggle" id="wrapper">
    <TheSidemenu />
    <!-- Page Content -->
    <div id="page-content-wrapper">
      <TheTopmenu />
      <div class="container-fluid">
        <div class="d-flex flex-row-reverse">
          <router-link
            :to="{ name: 'AddCategorythree' }"
            class="btn btn-sm btn-primary m-1"
          >
            <i class="fas fa-plus"></i>
            دسته جدید
          </router-link>
        </div>
        <div class="table-responsive">
          <table class="caption-top table table-hover table-striped">
            <caption>
              {{this.$route.meta.title}}
            </caption>
            <thead class="table-dark">
              <tr>
                <th scope="col">ردیف</th>
                <th scope="col">والد-دسته سطح یک</th>
                <th scope="col">نام</th>
                <th scope="col">شناسه</th>
                <th scope="col">ویرایش</th>
                <th scope="col">حذف</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in categorythreesList" :key="item.id">
                <th style="width: 3%" scope="row">
                  {{ index + 1 }}
                </th>
                <td>
                  {{ item.category_twos ? item.category_twos.name : '--' }}
                </td>
                <td>
                  {{ item.name }}
                </td>
                <td style="width: 3%">
                  {{ item.id }}
                </td>
                <td >
                  <router-link
                    :to="{ name: 'EditCategorythree', params: { categorythreeId: item.id } }"
                    class="btn btn-success btn-sm"
                  >
                    <i class="fas fa-edit"></i>
                    ویرایش
                  </router-link>
                </td>
                <td>
                  <a class="btn btn-sm btn-danger" @click="deleteItem(item.id)">
                    حذف
                  </a>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div
          v-if="lastPage > 1"
          class="d-flex justify-content-center bd-highlight mb-3"
        >
          <span
            v-if="showPreviusSpin"
            class="fas fa-spinner fa-spin mt-3"
          ></span>
          <a
            :disabled="currentPage > 1"
            :class="
              'btn btn-primary btn-sm m-1 ' +
              (currentPage > 1 ? '' : 'disabled')
            "
            @click="previusPage()"
          >
            قبلی
          </a>
          <select class="m-1 p-1" @change="changePage($event)">
            <option
              v-for="index in this.lastPage"
              :key="index"
              :selected="index == this.currentPage"
            >
              {{ index }}
            </option>
          </select>
          <a
            :disabled="currentPage != lastPage"
            :class="
              'btn btn-primary btn-sm m-1 ' +
              (currentPage != lastPage ? '' : 'disabled')
            "
            @click="nextPage()"
          >
            بعدی
          </a>
          <span v-if="showNextSpin" class="fas fa-spinner fa-spin mt-3"></span>
        </div>
      </div>
    </div>
    <!-- /#page-content-wrapper -->
  </div>
  <!-- /#wrapper -->
</template>

<script>
import TheSidemenu from "../components/TheSidemenu";
import TheTopmenu from "../components/TheTopmenu";
import ArefApi from "../libs/api.js";

export default {
  name: "ListCategorythrees",
  created: function () {
    this.arefApi = new ArefApi(this);
  },
  components: {
    TheSidemenu,
    TheTopmenu,
  },
  data() {
    return {
      categorythreesList:[],
      arefApi: new ArefApi(this),
      currentPage: this.$route.params.page ? this.$route.params.page :1,
      lastPage: null,
      showPreviusSpin: false,
      showNextSpin: false,
    };
  },
  methods: {
    getCategorythreesIndex: async function (page) {
      this.arefApi
        .getCategorythreesIndex(page)
        .then((categorythreesList) => {
          this.categorythreesList = categorythreesList.data;
          this.currentPage = categorythreesList.meta.current_page;
          this.lastPage = categorythreesList.meta.last_page;
        })
        .catch((err) => {
          this.$swal.fire({
            title: "خطا!",
            text: `سیستم با خطای ${err.status} مواجه شد`,
            icon: "error",
            confirmButtonText: "متوجه شدم",
          });
        });
    },
    nextPage: async function () {
      this.showNextSpin = true;
      this.currentPage++;
      this.$router.push(`/admin/listcategorythrees/${this.currentPage}`);
      await this.getCategorythreesIndex(this.currentPage);
      this.showNextSpin = false;
    },
    previusPage: async function () {
      this.showPreviusSpin = true;
      this.currentPage--;
      this.$router.push(`/admin/listcategorythrees/${this.currentPage}`);
      await this.getCategorythreesIndex(this.currentPage);
      this.showPreviusSpin = false;
    },
    changePage: function (event) {
      this.$router.push(`/admin/listcategorythrees/${event.target.value}`);
      this.getCategorythreesIndex(event.target.value);
    },
    deleteItem: function (productId) {
      this.$swal
        .fire({
          title: "اخطار حذف",
          text: "آیا حذف انجام شود؟",
          icon: "warning",
          showCancelButton: true,
          cancelButtonText: "خیر",
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "بله حذف انجام شود!",
        })
        .then((value) => {
          if (value.isConfirmed) {
            this.arefApi
              .deleteCategorythree(productId)
              .then(() => {
                this.$swal
                  .fire({
                    title: ``,
                    text: `حذف با موفقیت انجام شد`,
                    icon: "success",
                  })
                  .then(() => {
                    this.getCategorythreesIndex(this.currentPage);
                  });
              })
              .catch((err) => {
                this.$swal.fire({
                  title: `خطا`,
                  text: err.data.error,
                  icon: "error",
                });
              });
          }
        });
    },
  },
  mounted() {
    this.getCategorythreesIndex(this.currentPage);
  },
};
</script>

